import React, { PropTypes } from 'react';
import { Title4Top, Input, Center, Button3, Link, ErrorText } from '../styledComponents';

class PromoCodeField extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    const { promoCode, errorPromoCode, checkPromoCode } = this.props;
    return (
      this.state.showPromoCode ?
        <div>
          <Title4Top>Carte cadeau/code promo</Title4Top>
          <Center>
            <Input
              type="text" placeholder="code" value={this.state.promoCode}
              onChange={e => this.setState({ promoCode: e.target.value })}
            />
            <Button3 small noMargin onClick={() => checkPromoCode(this.state.promoCode)}>
              Appliquer</Button3>
          </Center>
          {promoCode && promoCode.code &&
            <Center>
              {promoCode.code} a bien t entr,
              {promoCode.value &&
                `${promoCode.value}${promoCode.unit === 'EUROS' ? '' : '%'} vous sont offerts sur cette commande.`}
            </Center>
          }
          {errorPromoCode &&
            <ErrorText>Code non reconnu</ErrorText>
          }
        </div> : <Center onClick={() => this.setState({ showPromoCode: true })}>
          <Link>+ Ajouter une carte cadeau ou un code promo</Link></Center>
    );
  }
}


PromoCodeField.propTypes = {
  promoCode: PropTypes.shape({}).isRequired,
  checkPromoCode: PropTypes.func.isRequired,
  errorPromoCode: PropTypes.bool,
};

PromoCodeField.defaultProps = {
  errorPromoCode: false,
};

export default PromoCodeField;



